.german {
  padding-top: 7rem;
  .german_container {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 1rem;
    a {
      display: flex;
      flex-direction: column;
      align-items: center;
      flex: 0.5 1 250px;
      padding: 1rem;
      border: 1px #DDBC9B solid;
      border-radius: 10px;
      text-decoration: none;
      position: relative;
      overflow: hidden;
      cursor: pointer;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      h3 {
        color: white;
        margin-top: 1rem;
        font-size: clamp(1.2rem, 1.5vw, 2rem);
      }
      .clip-red{
        background-color: red;
        box-shadow: 0px 4px 6px black;
        color: white;
        content: attr(data-clip);
        position: absolute;
        top:1.2rem;
        width: 14rem;
        right: -4rem;
        text-align: center;
        font-size: 1.3rem;
        transform: rotate(30deg);
        padding: 1%;
        z-index: 2;
      }
      .clip-green{
        background-color: green;
        box-shadow: 0px 4px 6px black;
        color: white;
        content: attr(data-clip);
        position: absolute;
        top:1.2rem;
        width: 14rem;
        right: -4rem;
        text-align: center;
        font-size: 1.3rem;
        transform: rotate(30deg);
        padding: 1%;
        z-index: 2;
      }
    }
  }
}
